<template>
  <div class="list">
    <div>
      <div class="books" v-for="(b,index) in bookList" @click="goDetails(b)">
        <div class="bLeft">
           <img :src="b.path" class="sImg">
        </div>
        <div class="sug tf">
          <div>{{b.name}}</div>
          <div class="word">{{b.synopsis}}</div>
          <div class="wsp">
            <span>
              <i class="mintui mintui-zuozhe"></i>{{b.author}}
            </span>
            <span class="sca">
              <span>{{b.type}}</span>
              <span>连载</span>
              <span>25万字</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {Indicator} from 'mint-ui';
  export default {
    name:'list',
    data(){
      return {
        bookList:[]
      }
    },
    created(){
      this.$route.meta.title = this.$route.params.name;
      Indicator.open('加载中');
      this.$axios.post('/vueBook/bookList',{})
      .then((res)=>{
        if(res.data.state === 200){
          Indicator.close();
          this.bookList = res.data.data;
        }
      })
      .catch((err)=>{
        console.log(err)
      });
    },
    methods:{
      goDetails(obj){
        this.$router.push({
          name:'/bookDetails',params:{name:obj.name},
        });
      }
    }
  }
</script>
<style scoped>
  .list {
    margin-top: 3rem;
  }
</style>


